/*
 * Gallery Page Styles
 */

@use "../../abstracts/" as *;

.gallery-wrapper {
  @include grid-generator(4, 12, 0.7);

  .gallery-item {
    position: relative;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .gallery-hvr-wrap {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      color: white;
      padding: 25px;
      align-items: flex-end;
      display: flex;
      background-color: black;
      cursor: pointer;
      opacity: 0;
    }

    &:hover .gallery-hvr-wrap {
      opacity: 0.9;
      visibility: visible;
    }

    .gallery-hvr-desc {
      transition: all 0.5s ease-in-out;

      h6 {
        margin-bottom: 0.2em;
      }

      p {
        font-size: 0.8rem;
      }
    }

    .btn-zoom {
      width: 80px;
      height: 80px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      color: white;
      display: block;
      margin: auto;

      &:hover {
        color: white;
      }

      img {
        max-width: 100%;
        margin: -20px auto 0 auto;
        display: block;
      }
    }
  }
}
